.title {
  font-size: 175%;
  font-family: 'Shrikhand', cursive;
  padding-top: 10px;
  padding-bottom: 10px;
  color: #e2ec4e;
  padding: 20px 0px;
  margin: auto;
  max-width: -webkit-max-content;
  max-width: max-content;
}

.ag-header-cell-label {
  justify-content: center;
  font-size: 20px;
}

.AgGridReact {
  --ag-background-color: #128b22;
  --ag-odd-row-background-color: ;
  --ag-header-background-color: #128b22;
  --ag-header-foreground-color: #e2ec4e;
  --ag-border-color: #e2ec4e;
  --ag-secondary-border-color: #e2ec4e;
  font-family: 'Amaranth', sans-serif !important;
  text-align: center;
  width: 100%;
  height: 500px;
}

button {
  background-color: #128b22;
  border: 3px solid #e2ec4e;
  color: #e2ec4e;
  padding: 15px 109px;
  text-align: center;
  display: inline-block;
  font-size: 20px;
  font-family: 'Amaranth', sans-serif;
}

.windowText {
  font-family: 'Amaranth', sans-serif;
  color: #e2ec4e;
  padding: 5px 5px;
  text-align: center; 
}

input {
  padding: 2.5px;
  margin-top: 5px;
  margin-bottom: 5px;
  background-color: #e2ec4e;
}
body {
  margin: auto;
  max-width: -webkit-max-content;
  max-width: max-content;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #1e6f30;
  padding: 0px 20px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

